@import "dialogs.less";
*,
*:before,
*:after {
	box-sizing: border-box;
}
body.editor {
	#toolbar {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		padding: 10px 10%;
		background: rgba(25, 25, 25, 0.75);
	}
	#toolbar button {  
		text-decoration: none;
		width: 30px;
		height: 30px;
		line-height: 1.7;
		text-align: center;
		vertical-align: middle;
		background: rgba(105, 105, 105, 0.7);
		border: 1px solid #777;
		color: white;
		font-size: 18px;
		border-radius: 1px;
		position: relative;
		padding: 0;
		cursor: pointer;

		transition: all .2s linear;

		i {
			font-style: normal;
		}

		&:hover {
			background: #999;
		}
		
		&.wysihtml5-command-active {
			background: rgba(255,255,255, 0.7);
			color: #111;

			&:before {
				height: 30px;
			}
			&:after {
				width: 30px;
			}
		}
	}
	#toolbar button.toolbar-right {
		float: right;
	}

	#editor {
		outline: 1px solid #eee;
		padding: 5px;
		margin-top: 0px;
		line-height: 1.3;
	}

	#editor img {
		max-width: 100%;
	}

	blockquote {
		margin: 0 0 0 10px;
		padding: 10px;
		border-left: 5px solid #ddd;
		font-size: 20px;
		color: #bbb;
		font-style: italic;
	}

	pre {
		background-color: #f5f5f5;
		border-radius: 5px;
		padding: 10px;
	}

	#content {
		outline: 1px solid #ddd;
	}

	#comments-wrapper {
		opacity: 0.1;
	}
}